body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(31,42,99);
  min-height: 100vh;
  background: linear-gradient(180deg, rgba(31,42,99,1) 0%, rgba(23,0,47,1) 98%);
  background-attachment: fixed;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
  height: 95px;
  padding: 0 80px 0 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 4px 4px 0px rgba(255, 255, 255, 0.25);
  position: sticky;
  top: 0;
  z-index: 4000;
  background: rgba(31, 42, 99, 1);
  @media (max-width: 600px) {
    padding: 0 20px 0 20px;
    gap: 25px;
  }
}

nav ul {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  gap: 30px;
  align-items: center;
  padding-left: 10px;
}

nav ul li {
  position: relative;
}

nav ul li::after {
  content: "";
  width: 0%;
  height: 2px;
  background-color: green;
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  transition: width 0.1s;
  border-radius: 2px;
}

nav ul li:hover::after {
  width: 120%;
}

nav ul li a {
  color: #F6F7F9!important;
  text-decoration: none;
  /*font-family: Karla;*/
  font-size: 25px;
  font-weight: 400;
  @media (max-width: 600px) {
    font-size: 20px;
  }
}

.homepage {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  color: #F6F7F9;
  padding: 30px 50px 0 100px;
  @media (max-width: 600px) {
      padding-left: 30px;
  }
}

.homepage-left {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 60px;
}

.home-title {
  font-size: 72px;
  @media (max-width: 600px) {
    font-size: 40px;
    line-height: 45px;
    letter-spacing: normal;
  }
  font-weight: 700;
  line-height: 84.17px;
  letter-spacing: 4px;
  text-align: left;
}

.home-subtitle {
  /*font-family: Karla;*/
font-size: 32px;
@media (max-width: 600px) {
  font-size: 22px;
  line-height: 27px;
}
font-weight: 400;
line-height: 37.41px;
text-align: left;
}

.button {
  box-shadow: 0px -9px 0px 0px rgba(0, 0, 0, 0.18) inset;
  color: black!important;
  text-decoration: none;
  border-radius: 12px;
  text-align: center;
  padding: 20px 40px 20px 40px;
  width: fit-content;
  font-weight: bold;
  transition: box-shadow 0.2s;
  cursor: pointer;
}

.button:hover {
  box-shadow: 0px -6px 0px 0px rgba(0, 0, 0, 0.18) inset;
}

.button.yellow {
  background-color: rgba(255, 193, 7, 1);
}

.button.green {
  background: rgba(115, 214, 119, 1);
}

.wrapper-404 {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

.centered-404 {
  position: relative;
  height: 500px;
  width: 100%;
    @media (max-width: 600px) {
      height: 400px;
    }
}

.huge-404 {
  text-align: center;
  width: 100%;
  position: absolute;
  margin: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    font-size: 550px;
    color: white;
    letter-spacing: 15px;
    @media (max-width: 600px) {
      font-size: 215px;
      letter-spacing: 30px;
    }
}

.img-404 {
  position: absolute;
  margin: 0;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
}

.svg-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 500px;
}

.cards {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  padding: 0;
}

.card {
  width: 300px;
  color: black;
  border-radius: 12px;
  background-color: aliceblue;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  cursor: pointer;
}

.characters-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 50px;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0,0,0,.6);
  z-index: 10000;
}

.modal-inner {
  width: 300px;
  height: fit-content;
  padding: 12px;
  border-radius: 12px;
  background-color: white;
  display: flex;
  flex-direction: column;
  gap: 15px;
  position: relative;
}

.modal-close {
  position: absolute;
top: 15px;
right: 15px;
cursor: pointer;
}

.loader {
    margin-left: 5px;
    width: 12px;
    height: 12px;
    border: 3px solid #FFF;
    border-bottom-color: #FF3D00;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
    }

    @keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
    }
